//axios.js
import axios from 'axios'
import store from './store'
import router from './router'
import Utils from './utils'
//axios
var instance = axios.create({
 timeout: 20000, //5
 headers: { 'Content-Type': 'application/json;charset=UTF-8' },
});
//request
instance.interceptors.request.use(
 config => {
  //tokenhttp headertoken
  if (store.state.token) {
   config.headers.Authorization = 'Bearer ' + store.state.token;
  }
  return config;
 }
);
//respone
instance.interceptors.response.use(
 response => {
  return response;
 },
 error => { //2XX
  console.log(error.message)
  if (error.message=="Network Error") {     // 
    Utils.toast("");
  }
  if(error.message.indexOf('timeout')!=-1 ){  // 
      Utils.toast("");
  }
  if (error.response) {
    console.log(error)
   switch (error.response.status) {
    case 401:
     // router.replace({ //
     //  path: 'login',
     //  query: { redirect: router.currentRoute.fullPath } // path
     // });
     Utils.dialog('','',[''],function () {
          store.dispatch('UserLogout');
          router.replace({ //
          path: 'login',
          query: { redirect: router.currentRoute.fullPath } // path
         });
       }
     )
      case 400: console.log('(400)') ; break;
      case 403: console.log('(403)'); break;
      case 404: console.log('(404)'); break;
      case 408: console.log('(408)'); break;
      case 500: console.log('(500)'); break;
      case 501: console.log('(501)'); break;
      case 502: console.log('(502)'); break;
      case 503: console.log('(503)'); break;
      case 504: console.log('(504)'); break;
      case 505: console.log('HTTP(505)'); break;
   }
  }
  
  return Promise.reject(error.response);
 }
);


export default instance
